@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}.center-text{text-align:center}.custom-wrapper{padding-left:5rem;padding-right:5rem}.custom-table{width:50%;margin:0 auto;z-index:0}.custom-burger{display:none}@media only screen and (max-width: 992px){display:block}.custom-mobile-menu{display:none}@media only screen and (max-width: 992px){display:block;position:absolute;width:100vw;height:100vh;text-align:center;top:70px;z-index:1}.custom-mobile-link{font-size:30px}.form{width:40%;margin:0 auto}@media only screen and (max-width: 992px){width:70%}.custom-header{text-align:center}.custom-label{font-size:1.5rem;color:rgba(0,0,0,0.8)}.mr5px{margin-right:5px}

body{background:#2d3b36 no-repeat center center fixed;background-size:cover}form{margin-left:auto;margin-right:auto;padding:30px;border:1px solid rgba(0,0,0,0.2);border-radius:7px;-moz-background-clip:padding;-webkit-background-clip:padding-box;background-clip:padding-box;background:rgba(0,0,0,0.5);box-shadow:0 0 13px 3px rgba(0,0,0,0.5);overflow:hidden}input{border:1px solid rgba(255,255,255,0.4);border-radius:4px;-moz-background-clip:padding;-webkit-background-clip:padding-box;background-clip:padding-box;display:block;font-family:'Source Sans Pro', sans-serif;font-size:18px;color:#fff;padding-left:20px;padding-right:20px;margin-bottom:20px}input[type=submit]{cursor:pointer}input.title{background:rgba(255,255,255,0.4) no-repeat scroll 16px 16px;padding-left:45px}input.author{background:rgba(255,255,255,0.4) no-repeat scroll 16px 20px;padding-left:45px}input.category{background:rgba(255,255,255,0.4) no-repeat scroll 16px 16px;padding-left:45px}::-webkit-input-placeholder{color:#fff}:-moz-placeholder{color:#fff}::-moz-placeholder{color:#fff}:-ms-input-placeholder{color:#fff}.btn{width:138px;height:44px;border-radius:4px;float:left;border:1px solid #253737;background:#416b68;background:-o-linear-gradient(top, #6da5a3, #416b68);background-image:-ms-linear-gradient(top, #6da5a3 0%, #416b68 100%);padding:0px 21px;border-radius:6px;box-shadow:rgba(255,255,255,0.1) 0 1px 0,inset rgba(255,255,255,0.7) 0 1px 0;text-shadow:#333333 0 1px 0;color:#e1e1e1}.btn:hover{border:1px solid #253737;text-shadow:#333333 0 1px 0;background:#416b68;background:-o-linear-gradient(top, #77b2b0, #416b68);background-image:-ms-linear-gradient(top, #77b2b0 0%, #416b68 100%);color:#fff}.btn:active{margin-top:1px;text-shadow:#333333 0 -1px 0;border:1px solid #253737;background:#6da5a3;background:-o-linear-gradient(top, #416b68, #6da5a3);background-image:-ms-linear-gradient(top, #416b68 0%, #6da5a3 100%);color:#fff;box-shadow:rgba(255,255,255,0) 0 1px 0,inset rgba(255,255,255,0.7) 0 1px 0}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}

